/**
 * a11y-dark theme for JavaScript, CSS, and HTML
 * Based on the okaidia theme: https://github.com/PrismJS/prism/blob/gh-pages/themes/prism-okaidia.css
 * @author ericwbailey
 */
code[class*="language-"],
pre[class*="language-"] {
  color: #5e5d6b;
  background: none;
  font-family: Consolas, Monaco, "Andale Mono", "Ubuntu Mono", monospace;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  line-height: 1.5;
  tab-size: 4;
  hyphens: none; }

/* Code blocks */
pre[class*="language-"] {
  padding: 1em;
  margin: 2rem 0;
  overflow: auto;
  border-radius: 0.3em;
  max-width: calc(100vw - 2rem); }

:not(pre) > code[class*="language-"],
pre[class*="language-"] {
  background: #fff; }

/* Inline code */
:not(pre) > code[class*="language-"] {
  padding: 0.1em;
  border-radius: 0.3em;
  white-space: normal; }

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: #76747f; }

.token.punctuation {
  color: #5e5d6b; }

.token.property,
.token.tag,
.token.constant,
.token.symbol,
.token.deleted {
  color: #4d48e8; }

.token.boolean,
.token.number {
  color: #348614; }

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
  color: #171392; }

.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.language-scss .token.string,
.style .token.string,
.token.variable {
  color: #d92d7a; }

.token.function {
  color: #6f34a3; }

.token.atrule,
.token.attr-value {
  color: #6b65e8; }

.token.keyword {
  color: #171392; }

.token.regex,
.token.important {
  color: #348614; }

.token.important,
.token.bold {
  font-weight: bold; }

.token.italic {
  font-style: italic; }

.token.entity {
  cursor: help; }

@media screen and (-ms-high-contrast: active) {
  code[class*="language-"],
  pre[class*="language-"] {
    color: windowText;
    background: window; }
  :not(pre) > code[class*="language-"],
  pre[class*="language-"] {
    background: window; }
  .token.important {
    background: highlight;
    color: window;
    font-weight: normal; }
  .token.atrule,
  .token.attr-value,
  .token.function,
  .token.keyword,
  .token.operator,
  .token.selector {
    font-weight: bold; }
  .token.attr-value,
  .token.comment,
  .token.doctype,
  .token.function,
  .token.keyword,
  .token.operator,
  .token.property,
  .token.string {
    color: highlight; }
  .token.attr-value,
  .token.url {
    font-weight: normal; } }

